.wizard {
  ul.wizard-list {
    display: table;
    list-style: none;
	width: 30%;
    margin: 0 0 20px;
	li {
      display: list-item;
	  height: 80px;
      position: relative;
	  &:first-child {
	    margin-top: 20px;
		&:before {
		  left: 49%;
		}
	  }
	  &:last-child:before {
	    left: 49%;
	    left: auto;
	    width: 0;
	  }
	  &:before {
	    content: "";
	    position: absolute;
	    height: 100%;
	    background: #ccc;
	    top: 20px;
	    width: 4px;
	    z-index: 4;
 	    left: 49%;
      }
	  &.disabled .step_no {
        background: #ccc;
      }
	  &.selected:before, .step_no {
        background: #34495E;
      }
	  &.done:before, &.done .step_no {
	    background: #1ABB9C;
	  }
	  .step_no {
	    width: 40px;
	    height: 40px;
	    line-height: 40px;
	    border-radius: 100px;
 	    display: block;
	    margin: 0 auto 5px;
	    font-size: 16px;
	    position: relative;
	    z-index: 5;
	  }
	  i {
	    font-style: normal;
		margin-left: 80px;
		white-space: nowrap;
		color: #73879c;
	  }
    }
  }
}
